import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SpeakersComponent } from '../views/speakers/speakers.component';
import { SpeakersContainerComponent } from '../views/speakers-container/speakers-container.component';
import { speakersDataResolver } from '../resolvers/speakers.resolver';

const routes: Routes = [
  {
    path: '',
    component: SpeakersContainerComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        component: SpeakersComponent,
        resolve: {
          speakers: speakersDataResolver
        }
      },
    ]
  },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class SpeakersRoutingModule { }
